body {
  margin: 0;
  font-family: "Helvetica";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#titi-nav ul li a span {
  background: #08F3CB !important;
  border-radius: 0 !important;
  width: 6px !important;
  height: 3px !important;
}

#titi-nav ul li:hover a span{
  width: 12px !important;
  margin: -2px 8px -2px -6px !important;
}

#titi-nav ul li a.active span,
.titi-slidesNav ul li a.active span {
  background: #08F3CB;

}

#titi-nav ul li:hover a.active span,
#titi-nav ul li a.active span,
.titi-slidesNav ul li:hover a.active span,
.titi-slidesNav ul li a.active span {
  height: 6px !important;
  width: 24px !important;
  margin: -6px 12px 0 -12px !important;
  border-radius: 0 !important;
}

